@font-face {
  font-family: 'Agustina Regular';
  font-style: normal;
  font-weight: normal;
  src: local('Agustina Regular'), url('./assests/fonts/Agustina.woff') format('woff');
}
@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat'), url('./assests/fonts/Montserrat-Regular.ttf') format('woff');
}

.main { 
  margin:0 auto;
  text-align: center;
} 

html, body {
  height: 100%;
  scroll-behavior: smooth;
  text-align: center;
}



.heading-text {
  text-align: center;
  margin:5px;
  margin-bottom: 30px;
  font-size: 25px;
  line-height: 1.1;
  font-weight: bolder;
}

.bottom-text {
  margin-top: 20px;
  font-size: 25px;
  line-height: 1.1;
  font-weight: bolder;
}


.small-text{
  margin-top: 20px;
  font-size: 13px;
  line-height: normal;
}

html {
  display: table;
  margin: auto;
}

body {
  display: table-cell;
  vertical-align: middle;

  margin: 0;
  font: 19px / 23px Montserrat, "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

.video {
  margin: auto;
  max-width: 720px;
  height: auto;
}



/* Media Query */
@media (max-width: 786px) {
  html,
  body {
    font-size: 16px;
    line-height: normal;
  }

  .heading-text {
    font-size: 16px;
    line-height: normal;
  }
  
  .bottom-text {
    font-size: 14px;
    line-height: normal;
  }

  .small-text{
    font-size: 12px;
    line-height: normal;
  }

  .video{
    margin: 5px;
  }
}
